<template lang="pug">
  .loader Loading...
</template>

<script>
export default {
  name:'loader'
}
</script>

<style lang="stylus">
.loader,
.loader:before,
.loader:after
  background #35495e
  -webkit-animation load1 1s infinite ease-in-out
  animation load1 1s infinite ease-in-out
  width 1em
  height 4em

.loader
  color #35495e
  text-indent -9999em
  margin 88px auto
  position relative
  font-size 11px
  -webkit-transform translateZ(0)
  -ms-transform translateZ(0)
  transform translateZ(0)
  -webkit-animation-delay -0.16s
  animation-delay -0.16s
  &:before
    left -1.5em
    -webkit-animation-delay -0.32s
    animation-delay -0.32s
  &:after
    left 1.5em

.loader:before,
.loader:after
  position absolute
  top 0
  content ''

@keyframes load1
    0%, 80%, 100%
      box-shadow 0 0
      height 4em
    40%
      box-shadow 0 -2em
      height 5em
</style>



